"use client"
import React, { useState } from 'react';

interface AnalysisResult {
  score: number;
  severity: string;
  recommendation: string;
}

const Analyze: React.FC = () => {
  const [text, setText] = useState<string>('');
  const [analysis, setAnalysis] = useState<AnalysisResult | null>(null);

  const performAnalysis = () => {
    const sensitivityScore = calculateSensitivity(text);
    setAnalysis({
      score: sensitivityScore,
      severity: getSeverityLevel(sensitivityScore),
      recommendation: getRecommendation(sensitivityScore)
    });
  };

  const calculateSensitivity = (inputText: string): number => {
    // API CALL
    return 100;
  };

  const getSeverityLevel = (score: number): string => {
    if (score < 20) return 'Low';
    if (score < 50) return 'Moderate';
    if (score < 80) return 'High';
    return 'Critical';
  };

  const getRecommendation = (score: number): string => {
    if (score < 20) return 'Content appears safe to share.';
    if (score < 50) return 'Consider reviewing before sharing.';
    if (score < 80) return 'Significant sensitivity detected. Careful review recommended.';
    return 'Content is highly sensitive. Do not share.';
  };

  const goBack = () => {
    window.location.href = '/';
  };

  return (
    <div className="min-h-screen bg-zinc-800 flex flex-col justify-center items-center p-4">
      <div className="max-w-2xl w-full bg-zinc-700 shadow-xl rounded-lg p-8 text-zinc-100">
        <div>
        <textarea 
          className="w-full h-48 p-4 bg-zinc-600 border border-zinc-500 text-zinc-100 rounded-lg mb-4 focus:outline-none focus:ring-2 focus:ring-indigo-500 placeholder-zinc-400"
          placeholder="Paste your text here to analyze its sensitivity..."
          value={text}
          onChange={(e) => setText(e.target.value)}
        />

        <button 
          className="w-full bg-indigo-600 hover:bg-indigo-500 text-white font-bold py-3 rounded-full transition duration-300 ease-in-out"
          onClick={performAnalysis}
        >
          Analyze Content
        </button>
        </div>
        <div>
                <div className="row">
            <div id="blah" className="col-lg-4 dropzone" ondrop="drag_drop(event)" ondragover="return false">
                <p>Drop Your Image Here</p>
            </div>
                <div className="col-lg-3">
                        <div className="form-group">
                            <label>File input</label>
                            <input type="file" id="userfile" name="userfile" />
                            <p className="help-block">Only image file allowed to upload here.</p>
                        </div>
                        <button className="btn btn-primary" onclick="uploadFile()">Upload</button>
                </div>
            </div>
        </div>

        {analysis && (
          <div className="mt-6 p-4 bg-zinc-600 rounded-lg">
            <h3 className="text-xl font-semibold mb-4 text-zinc-100">Analysis Results</h3>
            <p className="text-zinc-200"><strong className="text-zinc-100">Sensitivity Score:</strong> {analysis.score.toFixed(2)}/100</p>
            <p className="text-zinc-200"><strong className="text-zinc-100">Severity Level:</strong> {analysis.severity}</p>
            <p className="text-zinc-200"><strong className="text-zinc-100">Recommendation:</strong> {analysis.recommendation}</p>
          </div>
        )}
      </div>
    </div>
  );
};

export default Analyze;